import React from 'react';
import PropTypes from 'prop-types';
import Course from './finder__results__course';
import Profile from './finder__results__profile';

/**
 * Render a Funnelback result as the appropriate card type.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__course(props) {
    switch (props.type) {
        case 'course':
            return <Course details={props.details} query={props.query} />;
        case 'profile':
            return <Profile details={props.details} />;
    }
}

finder__results__course.propTypes = {
    details: PropTypes.object,
    query: PropTypes.object,
    type: PropTypes.string,
};

export default finder__results__course;
