import React from 'react';
import PropTypes from 'prop-types';

/**
 * Render a Funnelback best bet as a results card.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__bestbet(props) {
    return (
        <li className="finder__results__card finder__results__bestbet">
            <a href={'/fb' + props.details.clickTrackingUrl}>
                <div className="finder__results__card__details">
                    <h3 className="finder__results__card__heading underline-transition__title">
                        {props.details.title}
                    </h3>
                    <p className="finder__results__card__description">
                        {props.details.description}
                    </p>
                </div>
            </a>
        </li>
    );
}

finder__results__bestbet.propTypes = {
    details: PropTypes.object,
};

export default finder__results__bestbet;
