import React, { useState } from 'react';
import PropTypes from 'prop-types';

/**
 * Render radio facet.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__radio(props) {
    // Radio facets can be toggled open and closed
    // default state on load is defined in config
    // Always open on load if facet set.
    const [open, setOpen] = useState(
            props.query.facets[props.facet.meta] !== undefined
                ? true
                : props.facet.defaultOpen
        ),
        currentValue = props.query.facets[props.facet.meta] || '';

    // reduce the facet configuration to an array of all possible values for
    // the facet
    const allFacets = props.facet.values.reduce((acc, data) => {
        return [...acc, data.data.replace(/^"/, '').replace(/"$/, '')];
    }, []);

    // reduce the Funnelback response for the facet to an array of valid
    // values for the current query
    const responseFacets =
        props.responseFacet[0] &&
        props.responseFacet[0].categories[0] &&
        props.responseFacet[0].categories[0].values
            ? props.responseFacet[0].categories[0].values.reduce(
                  (acc, data) => {
                      return [...acc, data.data.toLowerCase()];
                  },
                  []
              )
            : [];

    // count how many possible facets are not valid for the current query
    // and will be hidden
    const hiddenFacets = allFacets.filter(
        facet => responseFacets.indexOf(facet) < 0
    ).length;

    const setFacet = e => {
        const newQuery = props.query,
            newValue = e.target.value;
        newValue
            ? (newQuery.facets[props.facet.meta] = newValue)
            : delete newQuery.facets[props.facet.meta];
        newQuery.startRank = 1;

        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    // the facet will be in the page twice, for desktop and mobile layouts
    // salting the input name to stop that breaking
    const randomNumber = Math.random()
        .toString(16)
        .slice(-4);

    return (
        <fieldset className="finder__filter finder__radio">
            <legend data-open={open ? 'true' : 'false'}>
                <button
                    type="button"
                    aria-expanded={open ? 'true' : 'false'}
                    onClick={() => setOpen(!open)}
                >
                    <span>
                        <span>{props.facet.name}</span>{' '}
                        <span
                            className={
                                open ? 'fal fa-minus icon' : 'fal fa-plus icon'
                            }
                            aria-label={open ? 'Close filter' : 'Open filter'}
                        />
                    </span>
                </button>
            </legend>
            <ul role="presentation">
                <li key={'all'}>
                    <input
                        checked={currentValue === ''}
                        id={`meta${props.facet.meta}all`}
                        name={`meta_${props.facet.meta}_orsand--${randomNumber}`}
                        type="radio"
                        value=""
                        onChange={e => setFacet(e)}
                    />
                    <label htmlFor={`meta${props.facet.meta}all`}>
                        {props.facet.noSelection}
                        {hiddenFacets > 0 && ` (${hiddenFacets} hidden)`}
                    </label>
                </li>

                {props.facet.values.map((value, i) => {
                    const responseFacetDetails =
                        props.responseFacet[0] &&
                        props.responseFacet[0].categories[0] &&
                        props.responseFacet[0].categories[0].values.filter(
                            responseFacetValue =>
                                responseFacetValue.data.toLowerCase() ===
                                value.data.replace(/^"/, '').replace(/"$/, '')
                        );

                    if (
                        (responseFacetDetails && responseFacetDetails[0]) ||
                        currentValue === value.data
                    ) {
                        return (
                            <li key={i}>
                                <input
                                    checked={currentValue === value.data}
                                    id={`meta${props.facet.meta}${i}`}
                                    name={`meta_${props.facet.meta}_orsand--${randomNumber}`}
                                    type="radio"
                                    value={value.data}
                                    onChange={e => setFacet(e)}
                                />
                                <label htmlFor={`meta${props.facet.meta}${i}`}>
                                    {value.label} (
                                    {responseFacetDetails &&
                                    responseFacetDetails[0]
                                        ? responseFacetDetails[0].count
                                        : 0}
                                    )
                                </label>
                            </li>
                        );
                    } else {
                        return null;
                    }
                })}
            </ul>
        </fieldset>
    );
}

finder__radio.propTypes = {
    facet: PropTypes.object,
    query: PropTypes.object,
    responseFacet: PropTypes.arrayOf(PropTypes.object),
    update: PropTypes.object,
};

export default finder__radio;
