import React from 'react';
import PropTypes from 'prop-types';
import formatLabel from '../results/finder__results__highlight__query';

/**
 * Render a Funnelback result as a course card.
 *
 * TODO: once the new collections are set up, standardise the metadata labels.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__results__course(props) {
    // Render course codes for courses that have and should display them
    // notably, not short courses
    const displayCourseCode = ['Undergraduate', 'Foundation', 'CPD'],
        courseCode = displayCourseCode.indexOf(props.details.metaData.L) >= 0 &&
            props.details.metaData.N && (
                <p className="finder__results__card__tag">
                    <span
                        className="fal fa-fw fa-graduation-cap icon"
                        aria-hidden="true"
                    />{' '}
                    <span>Course code:</span>{' '}
                    <span>{props.details.metaData.N}</span>
                </p>
            );

    // Degree certification, if it has it
    const award = props.details.metaData.M && (
        <p className="finder__results__card__tag">
            <span className="fal fa-fw fa-award icon" aria-hidden="true" />{' '}
            <span>Award:</span> <span>{props.details.metaData.M}</span>
        </p>
    );

    // Course duration, if it has it
    const duration = props.details.metaData.D && (
        <p className="finder__results__card__tag">
            <span className="fal fa-fw fa-clock icon" aria-hidden="true" />{' '}
            <span>Duration:</span> <span>{props.details.metaData.D}</span>
        </p>
    );

    // Course method, if it has it
    const method = props.details.metaData.metd && (
        <p className="finder__results__card__tag">
            <span className="fas fa-book-reader icon" aria-hidden="true" />{' '}
            <span>Method:</span> <span>{props.details.metaData.metd}</span>
        </p>
    );

    // Course location, if it has it
    const location = props.details.metaData.loc && (
        <p className="finder__results__card__tag">
            <span className="fas fa-map-marker-alt icon" aria-hidden="true" />{' '}
            <span>Location:</span> <span>{props.details.metaData.loc}</span>
        </p>
    );

    return (
        <li className="finder__results__card finder__results__course">
            <a href={props.details.clickTrackingUrl}>
                <div className="finder__results__card__details">
                    <h3 className="finder__results__card__heading underline-transition__title">
                        <span>
                            {props.query.query
                                ? formatLabel(
                                      props.details.title,
                                      props.query.query
                                  )
                                : props.details.title}
                        </span>
                    </h3>
                    <p className="finder__results__card__description">
                        {props.query.query
                            ? formatLabel(
                                  props.details.metaData.L,
                                  props.query.query
                              )
                            : props.details.metaData.L}
                    </p>
                    <p>
                        {props.query.query
                            ? formatLabel(
                                  props.details.metaData.c,
                                  props.query.query
                              )
                            : props.details.metaData.c}
                    </p>
                    {award}
                    {duration}
                    {courseCode}
                    {method}
                    {location}
                </div>
            </a>
        </li>
    );
}

finder__results__course.propTypes = {
    details: PropTypes.object,
    query: PropTypes.object,
};

export default finder__results__course;
