import React from 'react';
import { render } from 'react-dom';
import Finder from './patterns/finder/finder';
import { finderConfig } from './patterns/finder/funnelback';

document.addEventListener('DOMContentLoaded', () => {
    Array.from(document.querySelectorAll('.wrapper--finder')).forEach(
        finder => {
            finderConfig(finder.dataset.config).then(config => {
                render(<Finder config={config} />, finder);
            });
        }
    );
});
