import React from 'react';
import PropTypes from 'prop-types';

/**
 * Renders standard pagination controls patttern for the results.
 *
 * @param {object} props React props.
 * @return {object} - React component.
 */
function finder__pagination(props) {
    const numberOfPages = Math.ceil(props.totalMatching / props.numRanks),
        currentPage = Math.ceil(props.currStart / props.numRanks),
        pages = [];

    const changePage = pageNumber => {
        const newStartRank = 1 + (pageNumber - 1) * props.numRanks,
            newQuery = props.query;
        newQuery.startRank = newStartRank;
        props.update.query(newQuery);
        props.update.results(!props.update.updateState);
    };

    pages.push(
        <button
            className="pagination__controls__button--prev"
            key="prev"
            type="button"
            disabled={currentPage === 1 ? true : false}
            onClick={() => {
                changePage(currentPage - 1);
            }}
        >
            <span>Previous page</span>
        </button>
    );

    for (let page = 1; page <= numberOfPages; page++) {
        let className;
        switch (page - numberOfPages) {
            case 0:
                className =
                    'pagination__controls__element pagination__controls__button pagination__controls__button--last';
                break;
            case -1:
                className =
                    'pagination__controls__element pagination__controls__button pagination__controls__button--penultimate';
                break;
            default:
                className =
                    'pagination__controls__element pagination__controls__button';
        }

        pages.push(
            <button
                aria-current={page === currentPage ? 'page' : null}
                aria-expanded={page === currentPage ? true : false}
                aria-label={`Open page ${page}`}
                className={className}
                data-page={page}
                data-proximity={Math.abs(page - currentPage)}
                disabled={page === currentPage ? true : false}
                key={page}
                type="button"
                onClick={() => {
                    changePage(page);
                }}
            >
                <span>{page}</span>
            </button>
        );

        if (page === 1) {
            pages.push(
                <span
                    key="firstEllipsis"
                    className="pagination__controls__element pagination__controls__ellipsis pagination__controls__ellipsis--first"
                >
                    <span></span>
                </span>
            );
        } else if (page === numberOfPages - 1) {
            pages.push(
                <span
                    key="lastEllipsis"
                    className="pagination__controls__element pagination__controls__ellipsis pagination__controls__ellipsis--last"
                >
                    <span></span>
                </span>
            );
        }
    }

    pages.push(
        <button
            className="pagination__controls__button--next"
            key="next"
            type="button"
            disabled={currentPage === numberOfPages ? true : false}
            onClick={() => {
                changePage(currentPage + 1);
            }}
        >
            <span>Next page</span>
        </button>
    );

    return (
        <div className="pagination__wrapper">
            <nav className="pagination__controls">{pages}</nav>
        </div>
    );
}

finder__pagination.propTypes = {
    currStart: PropTypes.number,
    query: PropTypes.object,
    numRanks: PropTypes.number,
    totalMatching: PropTypes.number,
    update: PropTypes.object,
};

export default finder__pagination;
