import React from 'react';

const formatLabel = (label, value) => {
    if (!value) {
        return label;
    }
    return (
        <React.Fragment>
            {label.split(value).reduce((prev, current, i) => {
                if (!i) {
                    return [current];
                }
                return prev.concat(
                    <span className="highlightText" key={value + current}>
                        {value}
                    </span>,
                    current
                );
            }, [])}
        </React.Fragment>
    );
};

export default formatLabel;
